rabu (civilization) ")]),_c('v-card-text',[_vm._v(" Humans started to rapidly dominate the natural world enhancing their ability to build a community in incomparable scale than the other animals, and religions often was the main catalyst for their source of unity. In the early stage of human civilization, religion was an efficient methods to share the same perception of the world from different individuals, and even formed sense of morality later becoming a foundation for laws. ")])],1)],1),_c('v-dialog',{attrs:{"width":"850"},model:{value:(_vm.dialog3),callback:function ($$v) {_vm.dialog3=$$v},expression:"dialog3"}},[_c('v-card',[_c('v-img',{attrs:{"src":require("./images/3_.png")},on:{"click":function($event){_vm.dialog3 = false}}}),_c('v-card-title',{staticClass:"text-h5"},[_vm._v(" 極樂 ")]),_c('v-card-text',[_vm._v(" Buddhist idea of 極樂 (Guk-Lak) have two separated branch. First is 極樂淨土 (Guk-Lak-Jung-To), an idea that is similar to the Abraham branch religion’s heaven, where you must constantly work in order to achieve enlightenment to enter the heaven, while the other idea known as 唯心淨土 (Yu-Sim-Jung-To) suggests that heaven is already inside everyone’s heart, and we must get rid of all the greeds and worries and accept that our life itself already is a heaven. Throughout the history of mankind, humans have developed the society by sacrificing individual’s happiness in order to achieve each society’s perception of utopia. Perhaps in this time of rapid technological progress, we need to question ourselves if are these technological advancements are necessary to shape our society into a heaven. ")])],1)],1),_c('v-dialog',{attrs:{"width":"850"},model:{value:(_vm.dialog4),callback:function ($$v) {_vm.dialog4=$$v},expression:"dialog4"}},[_c('v-card',[_c('v-img',{attrs:{"src":require("./images/4_Haka.png")},on:{"click":function($event){_vm.dialog4 = false}}}),_c('v-card-title',{staticClass:"text-h5"},[_vm._v(" Haka ")]),_c('v-card-text',[_vm._v(" Civilization arose in the Pacific islands known as Polynesian has remarkable trait of sharing relatively the similar religious beliefs, culture, and language along the 1,000 islands in Pacific Ocean. Maori tribe is one of the most well-known tribe among them, famous for their ritual/performance at the beginning of the battle known as “Haka”, where warriors of the Maori tribe horrendous expressions with their face and movement. Haka is such a fascinating war tactics, as people believed that by performing the Haka, warriors absorbs the enemies “Mana”(spirit) from their fear, and as numbers of Polynesian society shared similar belief, Warriors of Maori were able to win numerous battles without shedding any bloods nor killing any people. ")])],1)],1),_c('v-dialog',{attrs:{"width":"600"},model:{value:(_vm.dialog5),callback:function ($$v) {_vm.dialog5=$$v},expression:"dialog5"}},[_c('v-card',[_c('v-img',{attrs:{"src":require("./images/5_Toad_s House.png")},on:{"click":function($event){_vm.dialog5 = false}}}),_c('v-card-title',{staticClass:"text-h5"},[_vm._v(" Toad’s House ")]),_c('v-card-text',[_vm._v(" In Korea, toads often symbolize the wisdom, rich, and stability. And if you take a brief look at the history of Korea, it is full of continuous internal changes brought by the invaders from different countries due to its geographical location. And often times it was the common people who were suffered and sacrificed from these drastic changes within the society. Originally, Toad’s house is a traditional children’s song and play from Korea, where kids sing “Toad, Toad, I will give my old house, so give me a new house” while digging a tunnel in the ground. Perhaps this absurd lyrics represent my ancestor’s desire to live in a stability after facing numerous shifts. ")])],1)],1),_c('v-dialog',{attrs:{"width":"600"},model:{value:(_vm.dialog6),callback:function ($$v) {_vm.dialog6=$$v},expression:"dialog6"}},[_c('v-card',[_c('v-img',{attrs:{"src":require("./images/6_UngNyeo.png")},on:{"click":functio